
.message {
    padding: 5px;
    font-size: 12px;
    display: inline-block;
    height: 26px;
    background-color: #4080ff;
    border-radius: 12px;
    color: #fff;
}
.message-wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding: 8px 5px;
}
.message-wrapper.me {
    -webkit-box-pack: end;
        -ms-flex-pack: end;
            justify-content: flex-end;
}
.message-wrapper.other {
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
}
.message-wrapper.other .message {
    background-color: green;
}



